<template>
  <div id="app">
    <h1>My To-Do List</h1>
    <to-do-form @todo-added="addToDo"></to-do-form>
    <h2 id="list-summary" ref="listSummary" tabindex="-1">{{listSummary}}</h2>
    <div ref="chart" style="width: 100%; height: 400px;"></div>
    <ul aria-labelledby="list-summary" class="stack-large">
      <li v-for="item in ToDoItems" :key="item.id">
        <to-do-item
          :label="item.label"
          :done="item.done"
          :id="item.id"
          @checkbox-changed="updateDoneStatus(item.id)"
          @item-deleted="deleteToDo(item.id)"
          @item-edited="editToDo(item.id, $event)">
        </to-do-item>
      </li>
    </ul>
    <!-- ECharts 热力图容器 -->
  </div>
</template>

<script>
import ToDoItem from "./components/ToDoItem.vue";
import uniqueId from "lodash.uniqueid";
import ToDoForm from "./components/ToDoForm.vue";
import * as echarts from 'echarts';  // 引入 ECharts

export default {
  name: "app",
  components: {
    ToDoItem,
    ToDoForm,
  },
  data() {
    return {
      ToDoItems: [
        { id: uniqueId("todo-"), label: "Learn Vue", done: false },
        {
          id: uniqueId("todo-"),
          label: "Create a Vue project with the CLI",
          done: true,
        },
        { id: uniqueId("todo-"), label: "Have fun", done: true },
        { id: uniqueId("todo-"), label: "Create a to-do list", done: false },
      ],
    };
  },
  methods: {
    addToDo(toDoLabel) {
      this.ToDoItems.push({ id: uniqueId('todo-'), label: toDoLabel, done: false });
    },
    updateDoneStatus(toDoId) {
      const toDoToUpdate = this.ToDoItems.find((item) => item.id === toDoId)
      toDoToUpdate.done = !toDoToUpdate.done
    },
    deleteToDo(toDoId) {
      const itemIndex = this.ToDoItems.findIndex((item) => item.id === toDoId);
      this.ToDoItems.splice(itemIndex, 1);
      this.$refs.listSummary.focus();
    },
    editToDo(toDoId, newLabel) {
      const toDoToEdit = this.ToDoItems.find((item) => item.id === toDoId);
      toDoToEdit.label = newLabel;
    },
    initChart() {
      // 初始化 ECharts 实例
      const chart = echarts.init(this.$refs.chart);

      // 热力图的配置项
      const options = {
        title: {
          text: 'To-Do List Task Completion Heatmap'
        },
        tooltip: {
          position: 'top'
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '5%',
          top: '5%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          data: ['Learn Vue', 'Create Project', 'Have Fun', 'Create To-Do List'],
          splitLine: { show: false }
        },
        yAxis: {
          type: 'category',
          data: ['Not Started', 'In Progress', 'Completed'],
          splitLine: { show: false }
        },
        visualMap: {
          min: 0,
          max: 1,
          calculable: true,
          orient: 'horizontal',
          left: 'center',
          bottom: '10%',
          color: ['#d94e5d', '#eac736', '#50a3ba']
        },
        series: [
          {
            name: 'Completion Status',
            type: 'heatmap',
            data: [
              [0, 0, this.ToDoItems[0].done ? 1 : 0], // 'Learn Vue' -> 完成状态
              [1, 0, this.ToDoItems[1].done ? 1 : 0], // 'Create Project' -> 完成状态
              [2, 0, this.ToDoItems[2].done ? 1 : 0], // 'Have Fun' -> 完成状态
              [3, 0, this.ToDoItems[3].done ? 1 : 0], // 'Create To-Do List' -> 完成状态
              [0, 1, this.ToDoItems[0].done ? 0 : 1], // 'Learn Vue' -> 待完成状态
              [1, 1, this.ToDoItems[1].done ? 0 : 1], // 'Create Project' -> 待完成状态
              [2, 1, this.ToDoItems[2].done ? 0 : 1], // 'Have Fun' -> 待完成状态
              [3, 1, this.ToDoItems[3].done ? 0 : 1], // 'Create To-Do List' -> 待完成状态
              [0, 2, this.ToDoItems[0].done ? 0 : 0], // 'Learn Vue' -> 未开始状态
              [1, 2, this.ToDoItems[1].done ? 0 : 0], // 'Create Project' -> 未开始状态
              [2, 2, this.ToDoItems[2].done ? 0 : 0], // 'Have Fun' -> 未开始状态
              [3, 2, this.ToDoItems[3].done ? 0 : 0], // 'Create To-Do List' -> 未开始状态
            ],
            label: {
              show: true,
              position: 'inside',
              color: '#fff'
            },
            emphasis: {
              itemStyle: {
                borderColor: '#333',
                borderWidth: 1
              }
            }
          }
        ]
      };

      // 设置图表的配置项
      chart.setOption(options);
    }
  },
  computed: {
    listSummary() {
      const numberFinishedItems = this.ToDoItems.filter((item) => item.done).length
      return `${numberFinishedItems} out of ${this.ToDoItems.length} items completed`
    }
  },
  mounted() {
    // 初始化图表
    this.initChart();
  }
};
</script>

